@layer components.notification {
  .notification {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: linear-gradient(90deg, #6a80f5, #515cb0);
    color: white;
    padding: 20px 40px;
    border-radius: 10px;
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
    font-size: 1.2rem;
    font-weight: bold;
    z-index: 1000;
    text-align: center;
    max-width: 80%;
    word-wrap: break-word;
  }
}
